    padding-bottom: 100px;
    margin-left: -8px;
    margin-right: -8px;
    margin-bottom: -8px;
}

.about-me {
    font-family: "Open Sans", sans-serif;
    font-size: 40px;
    letter-spacing: 2px;
    color: white;
    -webkit-text-stroke: 1px black;
    text-align: center;
