}

.text {
    font-family: "Open Sans", sans-serif;
    font-size: 40px;
    letter-spacing: 2px;
    color: white;
    text-align: left;
    padding-left: 100px;
    padding-right: 100px;
}

.error {
    font-size: 50px;
    text-align: center;
